import { useContext, useEffect, useMemo, useState } from "react";
import FormUtil from "../../../../../utils/form/formUtiil";
import ValidateUtil from "../../../../../utils/form/validateUtil";
import TreeUtil from "../../../../../utils/treeUtil";
import { FactoryTreeContext } from "../../logicMainteTab";
import LogicElementUtil from "../../util/logicElementUtil";
import LogicUtil from "../../util/logicUtil";
import ModifyOperations from "../common/modifyOperations";

const EditorSeedArgItem = () => {
    const { node, removeSelf, setModify, isModify, invalidate } = useContext(FactoryTreeContext);
    const wrap = node.data as LogicUtil.WrapElement;

    const [name, setName] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });
    const [min, setMin] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });
    const [max, setMax] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });
    const [step, setStep] = useState<FormUtil.CheckableValue>({ value: '1', errors: [] });
    const [defval, setDefval] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });
    // const [argType, setArgType] = useState<FormUtil.CheckableValue>({ value: 'number', errors: [] });

    /**  */
    const reservedList = useMemo(() => {
        const owner = (node.parent?.parent?.data as LogicUtil.WrapElement).data;
        return LogicElementUtil.getParamNameList(owner);
    }, [node]);

    const mappingForm = () => {
        const param = wrap.data as LogicUtil.NodeSeedArgItem;
        setName({ ...name, value: param.name });
        setMin({ ...min, value: param.min.toString() });
        setMax({ ...max, value: param.max.toString() });
        setStep({ ...step, value: param.step.toString() });
        setDefval({ ...defval, value: param.defval == undefined ? '' : param.defval.toString() });
        // setArgType({ ...argType, value: param.valueType });
    };

    /**  */
    const existNameList = useMemo(() => LogicElementUtil.getExistNameList(node), [node]);

    useEffect(() => {
        if (wrap.data == null) {
            setModify(true);
        } else {
            mappingForm();
        }
    }, [node]);

    return (<>
        <ModifyOperations.Component
            mappingForm={mappingForm}
            checkableFields={[name, min, max, step, defval]}
            firstRegist={() => {
                const param: LogicUtil.NodeSeedArgItem = {
                    name: name.value,
                    min: Number(min.value),
                    max: Number(max.value),
                    step: Number(step.value),
                    defval: Number(defval.value === '' ? undefined : defval.value),
                    // valueType: argType.value as LogicUtil.ArgumentType
                };
                wrap.data = param;
                // LogicElementUtil.syncArgumentAdd(node.parent?.parent as TreeUtil.ElementNode, name.value);
            }}
            modifyRegist={() => {
                const param = wrap.data as LogicUtil.NodeSeedArgItem;
                const prevName = param.name;
                param.name = name.value;
                param.min = Number(min.value);
                param.max = Number(max.value);
                param.step = Number(step.value);
                param.defval = Number(defval.value === '' ? undefined : defval.value);
                // param.valueType = argType.value as LogicUtil.ArgumentType;
                wrap.data = param;

                if (prevName !== name.value) {
                    // LogicElementUtil.syncArgumentName(node.parent?.parent as TreeUtil.ElementNode, prevName, name.value);
                }
            }}
        />
        <FormUtil.BorderPanel
            title="param"
            innerJsx={<>
                <FormUtil.FormRecord
                    titleLabel="Name"
                    jsx={
                        <FormUtil.TextField
                            width={200}
                            isReadOnly={!isModify}
                            checkable={name}
                            setCheckable={setName}
                            validates={[
                                {
                                    checker: (value) => ValidateUtil.checkRequired(value),
                                    errorType: "required"
                                },
                                {
                                    checker: (value) => ValidateUtil.checkStringLength(value, 1, 16),
                                    errorType: "length"
                                },
                                {
                                    checker: (value) => LogicUtil.checkFieldName(value),
                                    errorType: "value"
                                },
                                {
                                    checker: (value) => !existNameList.includes(value),
                                    errorType: "relation"
                                }
                            ]}
                        />
                    }
                />
                <FormUtil.FormRecord
                    titleLabel="Min"
                    jsx={
                        <FormUtil.TextField
                            width={100}
                            isReadOnly={!isModify}
                            checkable={min}
                            setCheckable={setMin}
                            isNumber
                            dependency={[max]}
                            validates={[
                                {
                                    checker: (value) => ValidateUtil.checkRequired(value),
                                    errorType: "required"
                                },
                                {
                                    checker: (value) => Number(value) < Number(max.value),
                                    errorType: "relation"
                                }
                            ]}
                        />
                    }
                />
                <FormUtil.FormRecord
                    titleLabel="Max"
                    jsx={
                        <FormUtil.TextField
                            width={100}
                            isReadOnly={!isModify}
                            checkable={max}
                            setCheckable={setMax}
                            isNumber
                            dependency={[min]}
                            validates={[
                                {
                                    checker: (value) => ValidateUtil.checkRequired(value),
                                    errorType: "required"
                                },
                                {
                                    checker: (value) => Number(value) > Number(min.value),
                                    errorType: "relation"
                                }
                            ]}
                        />
                    }
                />
                <FormUtil.FormRecord
                    titleLabel="Step"
                    jsx={
                        <FormUtil.TextField
                            width={100}
                            isReadOnly={!isModify}
                            checkable={step}
                            setCheckable={setStep}
                            isNumber
                            validates={[
                                {
                                    checker: (value) => ValidateUtil.checkRequired(value),
                                    errorType: "required"
                                }
                            ]}
                        />
                    }
                />
                <FormUtil.FormRecord
                    titleLabel="Default"
                    jsx={
                        <FormUtil.TextField
                            width={100}
                            isReadOnly={!isModify}
                            checkable={defval}
                            setCheckable={setDefval}
                            isNumber
                        />
                    }
                />
                {/* <FormUtil.FormRecord
                    titleLabel="Type"
                    jsx={
                        <FormUtil.Combobox
                            checkable={argType}
                            setCheckable={setArgType}
                            list={LogicUtil.ArgTypes.map(type => {
                                return { value: type, labelText: type, isDisabled: type === 'color' }
                            })}
                            headBlank
                            width={200}
                            isReadOnly={!isModify}
                            validates={
                                [
                                    {
                                        checker: (value) => ValidateUtil.checkRequired(value),
                                        errorType: "required"
                                    }
                                ]
                            }
                        />
                    }
                /> */}
            </>}
        />
        <FormUtil.BorderPanel
            title="operation"
            isEnable={!isModify}
            innerJsx={<>
                <FormUtil.ButtonRecord
                    align="center"
                    buttons={[
                        {
                            label: 'Delete',
                            isEnable: !isModify,
                            callback: () => {
                                // LogicElementUtil.syncArgumentDel(node.parent?.parent as TreeUtil.ElementNode, name.value);
                                removeSelf();
                            }
                        }
                    ]}
                />
                {/*  */}
                {LogicElementUtil.getSwapButtons(node, invalidate)}
            </>}
        />
    </>);
}

export default EditorSeedArgItem;